const actions = {
  getFixedAssetTransfers: (page, search, filter) => ({
    type: 'GET_FIXED_ASSET_TRANSFERS_REQUEST',
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  getImportFixedAssets: (page, search, filter) => ({
    type: 'GET_IMPORT_FIXED_ASSETS_FAT_REQUEST',
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createFixedAssetTransfer: () => ({
    type: 'CREATE_FIXED_ASSET_TRANSFER_REQUEST'
  }),
  storeFixedAssetTransfer: data => ({
    type: 'STORE_FIXED_ASSET_TRANSFER_REQUEST',
    payload: { body: data }
  }),
  editFixedAssetTransfer: id_fixed_asset_transfer => ({
    type: 'EDIT_FIXED_ASSET_TRANSFER_REQUEST',
    payload: { id_fixed_asset_transfer: id_fixed_asset_transfer }
  }),
  updateFixedAssetTransfer: (id_fixed_asset_transfer, data) => ({
    type: 'UPDATE_FIXED_ASSET_TRANSFER_REQUEST',
    payload: {
      id_fixed_asset_transfer: id_fixed_asset_transfer,
      body: data
    }
  }),
  showFixedAssetTransfer: id_fixed_asset_transfer => ({
    type: 'SHOW_FIXED_ASSET_TRANSFER_REQUEST',
    payload: { id_fixed_asset_transfer: id_fixed_asset_transfer }
  }),
  processFixedAssetTransfer: id_fixed_asset_transfer => ({
    type: "PROCESS_FIXED_ASSET_TRANSFER_REQUEST",
    payload: { id_fixed_asset_transfer }
  }),
  cancelFixedAssetTransfer: (id_fixed_asset_transfer, data) => ({
    type: "CANCEL_FIXED_ASSET_TRANSFER_REQUEST",
    payload: { 
      id_fixed_asset_transfer,
      body : data
    }
  }),
  uploadDocument: (id_fixed_asset_transfer, data) => ({
    type: "UPLOAD_FIXED_ASSET_TRANSFER_DOCUMENT_REQUEST",
    payload: {
      id_fixed_asset_transfer,
      body : data
    }
  }),
  downloadDocument: id_fixed_asset_transfer_document => ({
    type: "DOWNLOAD_FIXED_ASSET_TRANSFER_DOCUMENT_REQUEST",
    payload: {id_fixed_asset_transfer_document}
  }),
  deleteDocument: id_fixed_asset_transfer_document => ({
    type: "DELETE_FIXED_ASSET_TRANSFER_DOCUMENT_REQUEST",
    payload: { id_fixed_asset_transfer_document }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
