import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Link } from "react-router-dom";
import moment from "moment";

import notification from "../../../../components/notification";
import {
  Empty,
  Input,
  Tooltip,
  Collapse,
  Select,
  DatePicker,
  Row,
  Col
} from "antd";

// import Popconfirms from "../../../../components/feedback/popconfirm";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/asset/fixedAssetTransfer/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import NumberFormat from "../../../App/NumberFormat";
import CardWrapper, {
  Box,
  StatusTag,
  customCollapseStyle,
  StatusBadge,
  TransNo
} from "../../../App/app.style";
import getLanguage from "../../../App/MyLanguange";

const Panel = Collapse.Panel;
const Option = Select.Option;
const Search = Input.Search;
const { RangePicker } = DatePicker;

const redirectFA = "/dashboard/fixed-assets"

class FixedAssetTransfer extends Component {
  state = {
    filter: {
      id_from_location: null,
      id_to_location: null,
      status: null,
      start_date: null,
      end_date: null,
      pagination: null
    },
    search: null
  };

  // Get All data diawal
  componentDidMount() {
    const { getFixedAssetTransfers } = this.props;
    getFixedAssetTransfers();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.fixedAssetTransfer.error) {
      notification("error", this.props.fixedAssetTransfer.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.fixedAssetTransfer.success) {
      notification("success", this.props.fixedAssetTransfer.success);
      this.props.clearSuccess();
    }
  }

  // Handle search
  handleSearch = value => {
    const { getFixedAssetTransfers } = this.props;
    this.setState({
      filter: {
        id_from_location: null,
        id_to_location: null,
        status: null,
        start_date: null,
        end_date: null,
        pagination: null
      }
    });
    getFixedAssetTransfers(1, value);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getFixedAssetTransfers } = this.props;
    this.setState({
      filter: {
        id_from_location: null,
        id_to_location: null,
        status: null,
        start_date: null,
        end_date: null,
        pagination: null
      }
    });
    this.setState({ search: null });
    getFixedAssetTransfers();
  };

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  // Handle filter
  handleFilter = () => {
    const { getFixedAssetTransfers } = this.props;
    //const { current_page } = this.props.fixedAssetTransfer;
    const { filter } = this.state;
    let string = "";

    if (filter.id_from_location) {
      string += "id_from_location=" + filter.id_from_location;
    }
    if (filter.id_to_location) {
      string
        ? (string += "&id_to_location=" + filter.id_to_location)
        : (string += "id_to_location=" + filter.id_to_location);
    }
    if (filter.status) {
      string
        ? (string += "&status=" + filter.status)
        : (string += "status=" + filter.status);
    }
    if (filter.start_date && filter.end_date) {
      string
        ? (string +=
          "&start_date=" + filter.start_date + "&end_date=" + filter.end_date)
        : (string +=
          "start_date=" + filter.start_date + "&end_date=" + filter.end_date);
    }

    if (filter.pagination) {
      string
        ? (string += "&pagination=" + filter.pagination)
        : (string += "pagination=" + filter.pagination);
    }

    this.setState({ search: null });
    getFixedAssetTransfers(1, null, string);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getFixedAssetTransfers } = this.props;
    this.setState({
      filter: {
        id_from_location: null,
        id_to_location: null,
        status: null,
        start_date: null,
        end_date: null,
        pagination: null
      }
    });
    this.setState({ search: null });
    getFixedAssetTransfers();
  };

  // Handle action di table
  handleRecord = (action, id) => {
    if (action === "activate") {
      this.props.activateFixedAssetTransfer(id);
    } else {
      this.props.deactivateFixedAssetTransfer(id);
    }
  };

  // Handle pagination
  handlePageChange = page => {
    const { getFixedAssetTransfers } = this.props;
    const { search, filter } = this.props.fixedAssetTransfer;
    getFixedAssetTransfers(page, search, filter);
  };


  listAction = {
    title: "",
    dataIndex: "id_fixed_asset_transfer",
    rowKey: "id_fixed_asset_transfer",
    width: "10%",
    align: "center",
    fixed: "right",
    render: (text, row) => (
      <div>
        <Tooltip
          placement="topLeft"
          title={<IntlMessages id="tooltip.update" />}
        >
          <Link to={`${this.props.match.path}/update/${text}`}>
            <Button size="small" className="invoiceEdtBtn">
              <i className="ion-android-create" />
            </Button>
          </Link>
        </Tooltip>
      </div>
    )
  };

  render() {
    // console.log(this.props);

    // Data setup
    const { fixed_asset_transfers, from_locations, to_locations } = this.props.fixedAssetTransfer;
    //console.log(this.props.fixedAssetTransfer); //console.log(this.state);
    const dataSource = [];
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    const { loading, loadingForeign } = this.props.fixedAssetTransfer;

      // Definisi kolom2 serta field2 API yang dipakai tabel
    let columns = [
      {
        title: getLanguage({ id: "assetCode"}),
        dataIndex: "fixed_asset.fixed_asset_code",
        rowKey: "fixed_asset.fixed_asset_code",
        width: "15%",
        render: (text, row) => {
          let className;
          switch (row.status) {
            case 1:
              className = "new";
              break;
            case 2:
              className = "ordered";
              break;
            case 3:
              className = "cancelled";
              break;
            // case 4:
            //   className = "received";
            //   break;
            default:
              className = "new";
              break;
          }

          return (
            <div style={{ display: "flex", height: "20px" }}>
              <StatusBadge className={className} />
              <Link to={`${redirectFA}/update/${row.id_fixed_asset}`}>
                <TransNo>{text}</TransNo>
              </Link>

            </div>
          );
        }
      },
      {
        title: getLanguage({ id: "transferDate"}),
        dataIndex: "transfer_date",
        rowKey: "transfer_date",
        width: "10%",
        render: text => (
          <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>
        )
      },
      {
        title: getLanguage({ id: "assetName"}),
        dataIndex: "fixed_asset.fixed_asset_name",
        rowKey: "fixed_asset.fixed_asset_name",
        width: "20%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "fixedAssets.add.serialNo"}),
        dataIndex: "fixed_asset.serial_no",
        rowKey: "fixed_asset.serial_no",
        width: "20%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "fromLocation"}),
        dataIndex: "from_location_name",
        rowKey: "from_location_name",
        width: "20%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "toLocation"}),
        dataIndex: "to_location_name",
        rowKey: "to_location_name",
        width: "20%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "qtyTransferred"}),
        dataIndex: "qty",
        rowKey: "qty",
        width: "15%",
        align: "right",
        render: text =>
          <NumberFormat value={parseFloat(text)} />
      },
      {
        title: getLanguage({ id: "createdBy"}),
        dataIndex: "created_by",
        rowKey: "created_by",
        width: "10%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "status"}),
        dataIndex: "status",
        rowKey: "status",
        width: "10%",
        align: "center",
        render: text => {
          let className;
          let display;
          switch (text) {
            case 1:
              className = "new";
              display = getLanguage({ id: "new"});
              break;
            case 2:
              className = "ordered";
              display = getLanguage({ id: "processed"});
              break;
            case 3:
              className = "cancelled";
              display = getLanguage({ id: "cancelled"});
              break;
            // case 4:
            //   className = "received";
            //   display = "Received";
            //   break;
            default:
              className = "new";
              display = getLanguage({ id: "new"});
              break;
          }

          return <StatusTag className={className}>{display}</StatusTag>;
        }
      }
    ];

    //console.log(permissions);
    if (
      permissions.includes("Update Fixed Asset Transfer") &&
      columns.length === 9
    ) {
      columns.push(this.listAction);
    }

    Object.keys(fixed_asset_transfers).map((fixed_asset_transfer, index) => {
      return dataSource.push({
        ...fixed_asset_transfers[fixed_asset_transfer],
        key: fixed_asset_transfer
      });
    });

    // Pagination Setup
    const { current_page, page_size, total } = this.props.fixedAssetTransfer;

    //console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="fixedAssetTransfers.pageHeader" />}
            marginBottom={true}
          >
            {permissions.includes("Create Fixed Asset Transfer") ? (
              <Link to={`${this.props.match.path}/add`}>
                <Button
                  // loading={loading}
                  // disabled={loading}
                  type="primary"
                  className="mateAddInvoiceBtn"
                >
                  <IntlMessages id="fixedAssetTransfers.button.add" />
                </Button>
              </Link>
            ) : null}
          </PageHeader>
          <div className="isoInvoiceTableBtn">
            <Search
              placeholder={
                getLanguage({ id: "assetCode"}) + ", " +
                getLanguage({ id: "assetName"}) + ", " +
                getLanguage({ id: "fixedAssets.add.serialNo"}) + ", " +
                getLanguage({ id: "createdBy"})
              }
              onSearch={this.handleSearch}
              style={{ marginRight: "1%" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
            <div
              style={{
                // width: "20%",
                marginTop: "auto",
                marginRight: "1%"
              }}
            >
              <Button
                type="primary"
                onClick={this.handleSearch.bind(this, this.state.search)}
              >
                <IntlMessages id="button.search" />
              </Button>
            </div>

            <div
              style={{
                // width: "30%",
                marginTop: "auto",
                marginRight: "27%"
                // marginLeft: "-2%"
              }}
            >
              <Button onClick={this.handleResetSearch}>
                <IntlMessages id="filter.reset" />
              </Button>
            </div>

          </div>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="fromLocation" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_from_location"
                      )}
                      value={
                        this.state.filter.id_from_location
                          ? this.state.filter.id_from_location
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={
                        <IntlMessages id="fixedAssetTransfers.filter.select.location" />
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                    //showArrow={false}
                    >
                      {from_locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {data.location_code + " (" + data.location_name + ")"}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                  <Col span={12}>
                    <label>
                      <IntlMessages id="toLocation" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_to_location"
                      )}
                      value={
                        this.state.filter.id_to_location
                          ? this.state.filter.id_to_location
                          : []
                      }
                      style={{ width: "100%" }}
                      placeholder={
                        <IntlMessages id="fixedAssetTransfers.filter.select.location" />
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                    //showArrow={false}
                    >
                      {to_locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {data.location_code + " (" + data.location_name + ")"}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                </Row>
                <Row type="flex" justify="start">
                  <Col span={12}>
                    <label>
                      <IntlMessages id="fixedAssetTransfers.filter.date" />
                    </label>
                    <br />
                    <RangePicker
                      format="DD MMMM YYYY HH:mm:ss"
                      showTime
                      allowClear={true}
                      style={{ width: "95%" }}
                      value={
                        this.state.filter.start_date &&
                          this.state.filter.end_date
                          ? [
                            moment(this.state.filter.start_date),
                            moment(this.state.filter.end_date)
                          ]
                          : []
                      }
                      onChange={this.handleFilterChange.bind(this, "date")}
                    />
                  </Col>
                  <Col span={12}>
                    <Row justify="space-between" gutter={1} type="flex">
                      <Col span={8}>
                        <label>Status</label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "status"
                          )}
                          value={
                            this.state.filter.status
                              ? this.state.filter.status
                              : []
                          }
                          style={{ width: "95%" }}
                          placeholder="Status"
                          optionFilterProp="children"
                          filterOption={true}
                          // loading={this.props.location.loadingFilterSites}
                          allowClear={true}
                        //showArrow={false}
                        >
                          <Option value={1}> <IntlMessages id="new" /> </Option>
                          <Option value={2}> <IntlMessages id="processed" /> </Option>
                          <Option value={3}> <IntlMessages id="cancelled" /> </Option>
                          {/* <Option value={4}>Received</Option>  */}
                        </Select>
                      </Col>
                      <Col span={8}>
                        <label>
                          <IntlMessages id="dataPerPage" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "pagination"
                          )}
                          value={
                            this.state.filter.pagination
                              ? this.state.filter.pagination
                              : page_size
                          }
                          style={{ width: "95%" }}
                          // placeholder={
                          //   <IntlMessages id="pettyCashTypes.filter.select.transactionType" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                        //allowClear={true}
                        //showArrow={false}
                        >
                          <Option key={1} value={10}>
                            10
                          </Option>
                          <Option key={2} value={25}>
                            25
                          </Option>
                          <Option key={3} value={50}>
                            50
                          </Option>
                          <Option key={4} value={100}>
                            100
                          </Option>
                        </Select>
                      </Col>
                      <Col
                        span={8}
                        style={{
                          marginTop: "auto"
                        }}
                      >
                        <Button
                          size="small"
                          type="primary"
                          style={{ marginRight: "10px" }}
                          onClick={this.handleFilter}
                        >
                          <IntlMessages id="filter.submit" />
                        </Button>
                        <Button onClick={this.handleResetFilter} size="small">
                          <IntlMessages id="filter.reset" />
                        </Button>
                      </Col>
                    </Row>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {fixed_asset_transfers.length === 0 && !this.props.fixedAssetTransfer.loading ? (
              <Empty
                description={<IntlMessages id="fixedAssetTransfers.table.noData" />}
              />
            ) : (
                <div className="isoInvoiceTable">
                  <Scrollbars style={{ width: "100%" }}>
                    <TableWrapper
                      // size="small"
                      dataSource={dataSource}
                      columns={columns}
                      loading={loading}
                      className="invoiceListTable"
                      bordered={true}
                      // Set pagination, jika ada page_size mengunakan pagination API
                      // jika tidak ada page_size menggunakan pagination bawaan
                      pagination={
                        page_size
                          ? {
                            current: current_page,
                            pageSize: parseInt(page_size),
                            total: total,
                            onChange: this.handlePageChange.bind(this)
                          }
                          : true
                      }
                    />
                  </Scrollbars>
                </div>
              )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    fixedAssetTransfer: state.fixedAssetTransfer,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(FixedAssetTransfer);
